import * as THREE from 'three'
import { Pass, FullScreenQuad } from 'three/examples/jsm/postprocessing/Pass'
import fragment from './shaders/colormapFrag.glsl'
import vertex from './shaders/postproccessingVert.glsl'

import av from 'animate-value';

export class ColormapPass extends Pass {

	constructor(colormap) {
		super();

		this.material = new THREE.ShaderMaterial({
            uniforms: {
                tDiffuse: { type:"t", value: null },
                colorMap: { type:"t", value: colormap},
				oldColorMap: { type:"t", value: colormap},
				progress: { type:"f", value: 0.0 },
				opacity: { type:"f", value: 0.0 }
            },
            fragmentShader:fragment,
            vertexShader:vertex,
        })
		this.fsQuad = new FullScreenQuad(this.material)
	}

	updateIntensity(intensity){
		this.material.uniforms['intensity'].value = intensity
	}

	updateOpacity(opacity){
		this.material.uniforms['opacity'].value = opacity
	}

	changeColormap(colormap){
		this.material.uniforms['oldColorMap'].value = this.material.uniforms['colorMap'].value
		this.material.uniforms['colorMap'].value = colormap
		this.material.uniforms['progress'].value = 0.0
		av({
			from: 0.0,
			to: 1.0,
			duration: 1000,
			easing: 'easeInOutCubic',
			change: (progress) => {
				this.material.uniforms['progress'].value = progress
			}
		})
		
	}

	toggleOpacity(){
		if(this.material.uniforms['opacity'].value == 0.0){
			this.changeOpacity(1.0)
		}else{
			this.changeOpacity(0.0)
		}
	}

	changeOpacity(opacity){
		av({
			from: this.material.uniforms['opacity'].value,
			to: opacity,
			duration: 1000,
			easing: 'easeInOutCubic',
			change: (opacity) => {
				this.material.uniforms['opacity'].value = opacity
			}
		})
	}

	dispose() {
		this.material.dispose()
		this.fsQuad.dispose()
	}

	render(renderer, writeBuffer, readBuffer) {
		this.material.uniforms['tDiffuse'].value = readBuffer.texture

		if (this.renderToScreen) {
			renderer.setRenderTarget(null)
			this.fsQuad.render(renderer)
		} else {
			renderer.setRenderTarget(writeBuffer)
			if (this.clear) renderer.clear()
			this.fsQuad.render(renderer)
		}
	}
}