import * as THREE from 'three';
import fragment from './shaders/backgroundFrag.glsl'
import vertex from './shaders/backgroundVert.glsl'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';

import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass';
import { ColormapPass } from './ColormapPass.js';
import { BrightnessContrastShader } from 'three/examples/jsm/shaders/BrightnessContrastShader';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

import viridisColormap from './assets/colormaps/viridis.png'
import plasmaColormap from './assets/colormaps/plasma.png'
import redColormap from './assets/colormaps/reds.png'
import orangeColormap from './assets/colormaps/oranges.png'
import brownColormap from './assets/colormaps/browns.png'
import beigePinkColormap from './assets/colormaps/beige-pink.png'
import lightGrayColormap from './assets/colormaps/light-gray.png'
import blueColormap from './assets/colormaps/blues.png'
import beigeGrayColormap from './assets/colormaps/beige-gray.png'
import purpleColormap from './assets/colormaps/purples.png'

import av from 'animate-value';

export default class Background{
    constructor(){
        /* SETTING UP THE ASSETS */
        
        this.loadingManager = new THREE.LoadingManager();
        this.textureLoader = new THREE.TextureLoader(this.loadingManager);
        this.assetsInit();

        /* SETTING UP THE CAMERA */
        
        this.camera = new THREE.OrthographicCamera(-window.innerWidth /2, window.innerWidth/2, window.innerHeight/2, -window.innerHeight/2, 0.01, 100000 );
        //this.camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 0.01, 10000 );
        //this.camera.position.z = 1000;

        /* SETTING UP THE SCENE */

        this.scene = new THREE.Scene();
        this.scene.add( this.camera );

        /* SETTING UP THE RENDERER */
        this.canvas = document.querySelector("#scene")
        this.renderer = new THREE.WebGLRenderer( { antialias: true, canvas:this.canvas} );
        this.renderer.setSize( window.innerWidth, window.innerHeight );

        //const controls = new OrbitControls( this.camera, this.renderer.domElement );

        /* SETTING UP TIME */
        this.time = 0;
        this.spontaeousTimeIncrement = 0;

        /* SETTING UP THE MESH */
        this.addMesh();

        /* SETTING UP THE RENDER PASS AND POST-PROCESSING EFFETS */
        this.composer = new EffectComposer( this.renderer );
        this.composer.setSize( window.innerWidth, window.innerHeight );
        
        this.addPostProcessing();

        /* LAUNCHING THE RENDERING */
        this.render();
    }

    assetsInit(){
        this.colormaps = new Map()
        this.colormaps.set("red", [this.textureLoader.load(redColormap), "#bd2240"]) 
        this.colormaps.set("orange", [this.textureLoader.load(orangeColormap), "#641801"]) 
        this.colormaps.set("brown", [this.textureLoader.load(brownColormap), "#705152"]) 
        this.colormaps.set("beigePink", [this.textureLoader.load(beigePinkColormap), "#fddcd4"]) 
        this.colormaps.set("lightGray", [this.textureLoader.load(lightGrayColormap), "#e4dadf"]) 
        this.colormaps.set("blue", [this.textureLoader.load(blueColormap), "#166f9c"]) 
        this.colormaps.set("beigeGray", [this.textureLoader.load(beigeGrayColormap), "#6f6f6f"]) 
        this.colormaps.set("purple", [this.textureLoader.load(purpleColormap), "#5c236d"])
        this.colormapIndex = 0
    }

    addMesh(){
        //this.geometry = new THREE.PlaneGeometry( 100, 100 );
        this.geometry = new THREE.BoxGeometry( window.innerWidth, window.innerHeight, 10000 );
        this.material = new THREE.MeshNormalMaterial({side:THREE.DoubleSide});
        
        this.material = new THREE.ShaderMaterial({
            uniforms:{
                progress: {type: "f", value: 0},
                time: {type: "f", value: 0},
            },
            fragmentShader:fragment,
            vertexShader:vertex,
            side:THREE.DoubleSide
        })
        this.mesh = new THREE.Mesh( this.geometry, this.material );
        this.scene.add( this.mesh );
    }

    addPostProcessing(){
        this.renderPass = new RenderPass( this.scene, this.camera );
        this.composer.addPass( this.renderPass );

        // this.shaderPass = new ShaderPass( BrightnessContrastShader );
        // this.shaderPass.uniforms[ "contrast" ].value = 0.5;
        // this.shaderPass.uniforms[ "brightness" ].value = -0.;
        // this.composer.addPass( this.shaderPass );

        this.colormapPass = new ColormapPass(this.colormaps.get("brown")[0]);
        this.composer.addPass(this.colormapPass);
    }

    render(){
        this.resizeRendererToDisplaySize(this.composer.renderer)
        
        this.time++;
        this.time += this.spontaeousTimeIncrement;

        this.material.uniforms.time.value = this.time/50;
        
        //this.renderer.render( this.scene, this.camera );
        this.composer.render();
        
        window.requestAnimationFrame( this.render.bind(this) );
    }

    resize(){
        
        
    }

    resizeRendererToDisplaySize(renderer) {
        const canvas = this.canvas;
        const width = window.innerWidth;
        const height = window.innerHeight;
        const needResize = canvas.width != width || canvas.height != height;
        if (needResize) {
            console.log("resize")
            renderer.setSize(width, height, false);
            this.camera.aspect = width / height;
            this.camera.updateProjectionMatrix();
            canvas.width = width;
            canvas.height = height;
            canvas.style.width = width + "px";
            canvas.style.height = height + "px";
        }
        return needResize;
    }

    toggleColormap(){
        this.colormapPass.toggleOpacity()
    }

    changeColormap(colormapName){
        av({
            from: 0,
            to: 20,
            duration: 500,
            easing: 'linear',
            change: (value) => {this.spontaeousTimeIncrement = value}
        })
        av({
            from: 20,
            to: 0,
            duration: 500,
            delay: 500,
            easing: 'linear',
            change: (value) => {this.spontaeousTimeIncrement = value}
        })
        let colormap = this.colormaps.get(colormapName)
        this.colormapPass.changeColormap(colormap[0]);

        return colormap[1]
    }

    enableBlackAndWhite(){
        av({
            from: this.material.uniforms.progress.value,
            to: Math.abs(this.material.uniforms.progress.value - 1),
            duration: 1000,
            easing: 'easeInOutCubic',
            change: (value) => {this.material.uniforms.progress.value = value}
        })
        
    }
    
}