export function splitWords(str, callback) {
    var words = str.split(/\s+/);
    for (var i = 0; i < words.length; i++) {
        callback(words[i], i);
    }
}

export function normalizedSigmoid(len){
    function _sigmoid(x){
        return (1 / (1 + Math.exp(-x/len))) / 1 / (1 + Math.exp(-len));
    }

    return _sigmoid;
}