import Background from './Background.js'
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { splitWords } from './utils.js';
import { normalizedSigmoid } from './utils.js';

import fr from './assets/json/fr.json';
import frSources from './assets/json/fr_sources.json';

gsap.registerPlugin(ScrollTrigger);

const background = new Background();

const loaderWrapper = document.querySelector("#loader-wrapper");
const loader = document.querySelector("#loader");
const loaderFill = document.querySelector("#loader-bar-fill");
const languageSelector = document.querySelector("#language-selector");
const languageOptions = document.querySelectorAll(".language-option");

/* LOADING HANDLERS */

background.loadingManager.onProgress = (url, itemsLoaded, itemsTotal) => loaderFill.style.width = `${itemsLoaded / itemsTotal * 100}%`;
background.loadingManager.onLoad = () => setTimeout(() => languageSelector.classList.add("visible"), 500)

/* LANGUAGE SELECTION */ 

languageOptions.forEach((option) => {option.onclick = () => selectLangage(option.attributes["data-lang"].value)})
async function selectLangage(lang){
    loaderWrapper.classList.add("hidden");
    
    let json = lang=="fr" ? fr : en;
    let sourcesJson = lang=="fr" ? frSources : enSources;
    for(let key in json){
        let text = json[key]
        text = text.replaceAll("%s%", "<span class='emphasis'>")
        text = text.replaceAll("%/%", "</span>")
        text = text.replaceAll("%br%", "<br/>")
        document.querySelector(`#${key}`).innerHTML = text
    };

    let sources = document.querySelector("#sources")
    let i = 0
    for(let key in sourcesJson){
        let text = sourcesJson[key]
        text = text.replaceAll("%i%", "<span class='italic'>")
        text = text.replaceAll("%/i%", "</span>")
        let li = document.createElement("li")
        li.classList.add("hidden")
        li.setAttribute("data-delay", 125 + i*50)
        li.innerHTML = text
        sources.appendChild(li)
        i++
    }

    setTimeout(() => loaderWrapper.parentElement.removeChild(loaderWrapper), 500);
    setTimeout(() => background.colormapPass.changeOpacity(.5), 700)
    setTimeout(() => loadChapter(document.querySelector("#foreword")), 1000)
    // setTimeout(() => loadChapter(document.querySelector("#final")), 1000)
}

/* CHAPTERS APPERANCE FUNCTIONS */

function nextChapter(chapterName){
    let nextChapter = document.querySelector(`#${chapterName}`)
    let oldChapter = document.querySelector(".chapter:not(.none)")

    setTimeout(() => {
        let nbOfElemToHide = unloadChapter(oldChapter)
        setTimeout(() => loadChapter(nextChapter), 500 + nbOfElemToHide*100 + 1000);
    }, 1000);
}

function unloadChapter(chapter){
    let toHide = chapter.querySelectorAll("[data-toHide='true']")
    toHide.forEach((element, i) => element.style.transition = `transition: all 1000ms cubic-bezier(0.645, 0.045, 0.355, 1.000)`)
    toHide.forEach((element, i) => setTimeout(() => element.classList.add("hidden"), Math.min(i*100, 900)))
    let length = toHide.length;
    setTimeout(() => chapter.classList.add("none"), 500 + Math.min(length*100, 1100))
    setTimeout(() => window.scrollTo(0, 0), 500 + Math.min(length*100, 1100)+1)

    return length
}

function loadChapter(chapter){
    // Change background color
    background.colormapPass.changeOpacity(parseFloat(chapter.attributes["data-colormap-opacity"].value))
    let color = background.changeColormap(chapter.attributes["data-colormap"].value)
    document.querySelector("body").style.backgroundColor = color
    
    // Show chapter and add gsap animations
    chapter.classList.remove("none")
    let toShow = chapter.querySelectorAll(".hidden")
    let toGsap = chapter.querySelectorAll("[data-gsap='true']")

    toGsap.forEach((element, i) => {
        setTimeout(() => gsap.fromTo(element, JSON.parse(element.attributes["data-gsap-from"].value), JSON.parse(element.attributes["data-gsap-to"].value)), element.attributes["data-delay"].value)
    })
    toShow.forEach((element, i) => setTimeout(() => element.classList.remove("hidden"), 1 + parseInt(element.attributes["data-delay"].value)))
    //toShow.forEach((element, i) => setTimeout(() => {element.style["transition"] = ""}, 1 + parseInt(element.attributes["data-delay"].value) + 1000))
    
    // "read more" button
    readMoreObserver.observe(chapter.querySelector(".read-more"))

    if(chapter.id == "culture") gsapifyEmphasis();
}

/* CHAPTERS' CONTROLS SECTIONS */

let readMoreObserver = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {if(entry.isIntersecting) entry.target.classList.add("visible")})
}, {threshold: 1})

document.querySelectorAll(".read-more").forEach((readMore) => {
    let spans = [...readMore.children]
    let length = spans.length;
    let sigmoid = normalizedSigmoid(length);
    for(let i = 0; i < length; i++){
        spans[i].style.transitionDelay = `${sigmoid(i) * 1000}ms`;
    }
    readMore.onclick = () => {
        readMore.classList.remove("visible");
        readMore.classList.add("clicked");
        nextChapter(readMore.attributes["data-next"].value)
    };
})

/* SPECIFIC DETAILS */

// Underline in "culture" quotes

function gsapifyEmphasis(){
    let spans = document.querySelectorAll(".emphasis")
    spans.forEach((span) => {
        gsap.to(span, {
            opacity:1, 
            css: { backgroundColor: "rgba(255,255,255,0.25)" },
            duration: 0.5,
            rotationY: 90,
            ease: "none",
            
            scrollTrigger: {
                trigger: span, 
                start: "top center",
            }
        })
    })
}



